


































import {Component, Prop, Vue} from "vue-property-decorator";
// eslint-disable-next-line no-unused-vars
import ZaehlstelleSearchObject from "@/types/zaehlstelle/ZaehlstelleSearchObject";

/**
 * Der YesNo-Dialog ist ein generischer Dialog zur binren Abfrage beim Nutzer.
 * So kann z.B. die Besttigung fr das Lschen einer Entitt damit realisiert werden.
 *
 * Da das Besttigen einer Aktion in der Regel mit einem Button zusammenhngt, bietet der
 * YesNoDialog diesen gleich mit an. ber `buttontext` und `icontext` kann dieser konfiguriert werden.
 *
 * Wenn sowohl kein `buttontext` als auch `icontext` nicht gesetzt sind, kann der YesNoDialog auch
 * als reiner Dialog verwendet werden. Hierzu wird das Value vom Dialog durchgereicht.
 *
 * Die Besttigung des Dialogs wird ber ein `yes` Event signalisiert. Analog erfolgt die
 * Signalisierung der Abweisung durch ein `no` Event.
 *
 * Beispiel:
 * <yes-no-dialog
 *    v-model="deleteDialog"
 *    buttontext="Lschen"
 *    dialogtitle="Lschen?"
 *    dialogtext="Wollen Sie die Entitt wirklich lschen?"
 *    @no="deleteDialog = false"
 *    @yes="deleteSome"></yes-no-dialog>
 */

@Component
export default class MyDialog extends Vue {
  /**
   * Steuerflag fr den Dialog
   */
  @Prop()
  value!: boolean;

  @Prop()
  zaehlstelle!:ZaehlstelleSearchObject;


  closeDialog(): void {
    // this.value = false;
    this.$emit('closeDialog');
  }
}
