export const enum Levels {
  INFO = 'info',
  SUCCESS = 'success',
  WARNING = 'warning',
  ERROR = 'error'
}

export class ApiError extends Error {
  level: Levels;

  constructor(level?: Levels, message?: string, error?: string) {
    // bergibt die verbleibenden Parameter (einschlielich Vendor spezifischer Parameter) dem Error Konstruktor
    super(message);

    // Behlt den richtigen Stack-Trace fr die Stelle bei, an der unser Fehler ausgelst wurde
    this.stack = new Error().stack;

    // Benutzerdefinierte Informationen
    if (level === undefined) {
      level = Levels.ERROR;
    }
    if (message === undefined) {
      message = "Ein unbekannter Fehler ist aufgetreten, bitte den Administrator informieren.";
    }
    this.level = level;
    this.message = `${message}${error}`;
  }
}
