enum Zaehlart {
  KLASSISCH = 'K', /** Klassisch */
  HAUPTVERKEHRSRICHTUNG = 'H', /** Hauptverkehrsrichtung/Oberflche/Hoch */
  QUERSCHNITT = 'Q', /** Querschnitt */
  QUERSCHNITT_SONDERZAEHLUNG = 'Q_', /** Querschnitt/Sonderzhlung */
  BAHNSCHNITT = 'QB', /** Bahnschnitt */
  QUERSCHNITT_HAUPTVERKEHRSRICHTUNG = 'QH', /** Querschnitt/Hauptverkehrsrichtung/ Oberflche/Hoch */
  ISARSCHNITT = 'QI', /** Isarschnitt */
  STADTGRENZENZAEHLUNG = 'QS', /** STADTGRENZENZHLUNG */
  QUERSCHNITT_TUNNEL = 'QT', /** Querschnitt Tunnel/Unterfhrung/Tief */
  QUERSCHNITT_RADVERKEHR = 'QR', /** Querschnitt Radverkehr */
  RADVERKEHRSZAEHLUNG = 'R', /** Radverkehrszhlung */
  TUNNEL = 'T', /** Tunnel / Unterfhrung / Tief */
  TEILKNOTEN = 'TK' /** Teilknoten */
}

export const ZaehlartToBeschreibung = new Map<string, string>([
  [Zaehlart.KLASSISCH, '#'],
  [Zaehlart.HAUPTVERKEHRSRICHTUNG, 'Hauptverkehrsrichtung/Oberflche/Hoch'],
  [Zaehlart.QUERSCHNITT, 'Querschnitt'],
  [Zaehlart.QUERSCHNITT_SONDERZAEHLUNG, 'Querschnitt/Sonderzhlung'],
  [Zaehlart.BAHNSCHNITT, 'Bahnschnitt'],
  [Zaehlart.QUERSCHNITT_HAUPTVERKEHRSRICHTUNG, 'Querschnitt/Hauptverkehrsrichtung/ Oberflche/Hoch'],
  [Zaehlart.ISARSCHNITT, 'Isarschnitt'],
  [Zaehlart.STADTGRENZENZAEHLUNG, 'Stadtgrenzenzhlung'],
  [Zaehlart.QUERSCHNITT_TUNNEL, 'Querschnitt Tunnel/Unterfhrung/Tief'],
  [Zaehlart.QUERSCHNITT_RADVERKEHR, 'Querschnitt Radverkehr'],
  [Zaehlart.RADVERKEHRSZAEHLUNG, 'Radverkehrszhlung'],
  [Zaehlart.TUNNEL, 'Tunnel / Unterfhrung / Tief'],
  [Zaehlart.TEILKNOTEN, 'Teilknoten']
]);

export const BeschreibungToZaehlart = new Map([...ZaehlartToBeschreibung].reverse());
export default Zaehlart;